var pageOptions;
export default {
	data() {
		return {
			oldVal: '',
			newVal: ''
		}
	},
	onLoad(options) {
		console.log(options);
		pageOptions = options;

		if (options.name) {
			this.oldVal = options.name;
			this.newVal = options.name;
		}

	},
	methods: {

		save() {
			//保存文章分类信息
			let params = {};
			params.name = this.newVal;
			if (pageOptions._id) {
				params._id = pageOptions._id;
			}

			uni.request({
				url: this.apiBasic + '/cate',
				method: 'POST',
				data: params,
				header:{
					'Authorization':'Bearer '+uni.getStorageSync("SToken")
				},
				success: res => {
					console.log(res);
					uni.showToast({
						title: '保存成功'
					});
					setTimeout(function() {
						//保存成功后 跳转回 分类列表页面
						uni.redirectTo({
							url: '/pages/cate/cate'
						});
					}, 100);
				},
				fail: () => {},
				complete: () => {}
			});


		}

	}
}
